import util from '../util/util.js'
export default {
	data() {
		return {
			pageData : {
				company: '',
				post: '',
				education: '请输入学历',
				level: '985/211/普通',
				location: '',
				salary: '',
				description: ''
			},
			educationList: ['博士海归','硕士海归','本科海归','博士','硕士','本科','专科',],
			levelList: ['海外高校','985','211','普通']
		}
	},
	methods: {
		init() {
			
		},
		goHome() {
			uni.navigateTo({
				url : '/pages/index/index',
				success() {
					// let page = getCurrentPages()
					// console.log(page[0].onLoad())
				}
			})
		},
		change(e){
			let type = e.target.dataset.type
			let value = e.detail.value
			switch(type) {
				case 'company':
					this.pageData.company = value
					break
				case 'post':
					this.pageData.post = value
					break
				case 'education':
					this.pageData.education = this.educationList[value]
					break
				case 'level':
					this.pageData.level = this.levelList[value]
					break
				case 'location':
					this.pageData.location = value
					break
				case 'salary':
					this.pageData.salary = value
					break
				case 'description':
					this.pageData.description = value
					break
			}
			console.log('FMouse',this.pageData)
		},
		legalData() {
			for(let key in this.pageData){
				if (!this.pageData[key] && key !== 'description') {
					return false
				}
			}
			return true
		},
		submit() {
			let that = this
			let date = util.myformatter(new Date())
			if (that.legalData()) {
				const {data} = util.request({...that.pageData,comment:[],reliability:[1,0],date:date},'addMessage')
				setTimeout(function(){
					uni.showToast({
						title:'提交成功~感谢您的支持',
						icon: 'success'
					})
					that.pageData = {
						company: '',
						post: '',
						education: '请输入学历',
						level: '985/211/普通',
						location: '',
						salary: '',
						description: ''
					}
				},500)
			} else {
				uni.showToast({
					title: '请输入完整数据哦~',
					icon: 'none'
				})
			}
		}
	}
}
